import Vue from 'vue'
import Router from 'vue-router'
import Event from '@/components/Event'
import Grid from '@/components/EventListGrid'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/event',
      name: 'Event',
      component: Event
    },
    {
      path: '/',
      name: 'Grid',
      component: Grid
    }
  ]
})



